(function () {
  'use strict';

  /** @ngInject */
  function studentCtrl($scope, Common, User) {
    var vm = $scope;
    var student_model_template_url = 'app/pages/dashboard/student/student-model/student-model.html';
    var archive_model_template_url = 'app/pages/dashboard/student/archive-model/archive-model.html';

    vm.tableColumns = [
      {name: '', col: 'chinese_name', show: true},
      {name: '', col: 'sexual', show: true},
      {name: '', col: 'age', show: true},
      {name: '', col: 'school', show: true, html: cutHtml},
      {name: '', col: 'location', show: true, html: cutHtml},
      {name: '', col: 'course_name', show: true, html: cutHtml},
      {name: '', col: 'phone', show: true},
      {name: '', col: 'update_time', show: true, sort: 'order_update_time'},
      {name: '', col: 'id', show: true, class: 'option', html: optionHtml, handler: [view, edit, archive]}
    ];
    vm.tableState = {
      sort: {},
      search: {
        key: ''
      },
      pagination: {
        page: 1,
        page_size: 10,
        page_total: 50
      }
    };
    vm.tableConfig = {
      add: true,
      search: true
    };

    function cutHtml(data) {
      var html;
      if (data.length > 10) {
        var str = data.substring(0, 10) + '...';
        html = '<span title="' + data + '">' + str + '</span>'
      } else {
        html = data
      }
      return html;
    }

    function optionHtml(data) {
      var html = '<a ng-click="(item.handler[0])(data)" title=""><i class="iconfont icon-magnifier"></i></a>' +
        '<a ng-click="(item.handler[1])(data)" title=""><i class="iconfont icon-pencil"></i></a>' +
        '<a ng-click="(item.handler[2])(data)" title=""><i class="iconfont icon-archive"></i></a>' +
        '<a href="/export/user/info?user_id=' + data + '" target="_blank" title=""><i class="iconfont icon-print"></i></a>';
        //'<a href="/export.html#/?user_id=' + data + '" target="_blank" title=""><i class="iconfont icon-print"></i></a>';
      return html
    }

    vm.add = function () {
      Common.model.promptModel('studentModelCtrl', student_model_template_url, 'md', true, 'common-modal', {
        add: true,
        getdata: vm.pipe
      })
    };

    function edit(data) {
      User.get(data.id).then(function (res) {

        res.enrollment_time = res.enrollment_time ? new Date(res.enrollment_time) : null;

        Common.model.promptModel('studentModelCtrl', student_model_template_url, 'md', true, 'common-modal', {
          edit: true,
          item: res,
          getdata: vm.pipe
        })
      })
    }

    function view(data) {
      User.get(data.id).then(function (res) {
        Common.model.promptModel('studentModelCtrl', student_model_template_url, 'md', true, 'common-modal', {
          view: true,
          item: res
        })
      })
    }

    function archive(data) {
      Common.model.promptModel('archiveModelCtrl', archive_model_template_url, 'md', true, 'common-modal', {
        id: data.id,
        getdata: vm.pipe
      })
    }

    vm.callServer = function callServer(queryStr) {
      User.getStudentList(queryStr).then(function (res) {
        vm.tableData = res.items;
        vm.tableState.pagination.page = res.page;
        vm.tableState.pagination.page_total = res.page_total;
      })
    };

    vm.search = function () {
      vm.pipe(1);
    };

    vm.focus = function () {
      vm.tableState.search.key = '';
    }

  }

  angular.module('app.pages.dashboard.student').controller('studentCtrl', studentCtrl);
})();

