(function () {
  'use strict';

  /** @ngInject */
  function teacherCtrl($scope, User, Common, toastr) {
    var vm = $scope;
    var teacher_model_template_url = 'app/pages/dashboard/site/teacher/teacher-model/teacher-model.html';

    vm.tableColumns = [
      {name: '', col: 'chinese_name', show: true},
      {name: '', col: 'english_name', show: true},
      {name: '', col: 'graduated', show: true},
      {name: '', col: 'major', show: true},
      {name: '', col: 'country', show: true},
      {name: '', col: 'phone', show: true},
      {name: '', col: 'update_time', show: true, sort: 'order_update_time', html: updateTimeHtml},
      {name: '', col: 'id', show: true, class: 'option', html: optionHtml, handler: [view, edit, deleteItem]}
    ];
    vm.tableState = {
      sort: {},
      pagination: {
        page: 1,
        page_size: 10,
        page_total: 50
      }
    };
    vm.tableConfig = {
      add: true,
      delete: true,
      select: true
    };

    vm.callServer = function callServer(queryStr) {
      User.getTeacherList(queryStr).then(function (res) {
        vm.tableData = res.items;
        vm.tableState.pagination.page = res.page;
        vm.tableState.pagination.page_total = res.page_total;
      })
    };

    function updateTimeHtml(data) {
      return moment(data).format("YYYY-MM-DD");
    }

    function optionHtml() {
      var html = '<a ng-click="(item.handler[0])(data)" title=""><i class="iconfont icon-magnifier"></i></a>' +
        '<a ng-click="(item.handler[1])(data)" title=""><i class="iconfont icon-pencil"></i></a>' +
        '<a ng-click="(item.handler[2])(data)" title=""><i class="iconfont icon-delete"></i></a>';
      return html
    }

    vm.add = function () {
      Common.model.promptModel('teacherModelCtrl', teacher_model_template_url, 'md', true, 'common-modal', {
        add: true,
        getdata: vm.pipe
      })
    };

    function edit(data) {
      User.get(data.id).then(function (res) {

        Common.model.promptModel('teacherModelCtrl', teacher_model_template_url, 'md', true, 'common-modal', {
          edit: true,
          item: res,
          croppedDataUrl: res.photo,
          getdata: vm.pipe
        })
      })

    }

    function view(data) {
      User.get(data.id).then(function (res) {
        Common.model.promptModel('teacherModelCtrl', teacher_model_template_url, 'md', true, 'common-modal', {
          view: true,
          item: res,
          croppedDataUrl: res.photo
        })
      })
    }

    vm.deleteAll = function () {
      var selectIds = Common.table.selectIds(vm.tableData, 'id');
      if (!selectIds.length) {
        toastr.warning('', '', {timeOut: 2000});
        return;
      }

      _delete(selectIds);

    };

    function deleteItem(data) {
      _delete(data.id);
    }

    function _delete(ids) {
      Common.model.promptModel('deleteModelCtrl', 'app/components/delete-model/delete-model.html', 'sm', true, 'delete-modal', {prompt: '?'})
        .result.then('', function (data) {
        if (data === 'ok') {
          User.delete(ids).then(function (res) {
            toastr.success('', '', {timeOut: 2000});
            vm.pipe(1);
          });
        }
      });

    }

  }

  angular.module('app.pages.dashboard.site.teacher').controller('teacherCtrl', teacherCtrl);
})();

